
import LoginRegis from './Creatuser/LoginRegis';
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Home from './Trangchu/Home';
import User from './User/User';
import Toan from './Khoahoc/Toan';
import Nguvan from './Khoahoc/Nguvan';
import Tienanh from './Khoahoc/Tienanh';
import Hoa from './Khoahoc/Hoa';
import Sinh from './Khoahoc/Sinh';
import Vatly from './Khoahoc/Vatly';
import Su from './Khoahoc/Su';
import Dia from './Khoahoc/Dia';
import LoginAdmin from './Admin/LoginAdmin';
import BoardAdmin from './Admin/BoardAdmin';
import Vaokhoahoc from './Vaokhoahoc/Vaokhoahoc';
import Themtailieu from './Admin/Themtailieu';
import AdminCourse from './Admin/ListCourse/AdminCourse';
import Naptien from './Trangchu/Naptien ';
import ListCate from './listcategory/ListCate';
import React, { useState, useEffect , useRef} from 'react';
import RootCourse from './rootKhoahoc/RootCourse';
import Hocthu from './Hocthu/Hocthu';
import Hocreal from './Hocreal/Hocreal';

function App() {

  const [userPath, setUserPath] = useState([]);
  const [token, setToken] = useState(window.localStorage.getItem("token"));
  // Lưu trữ giá trị token trước đó bằng useRef
  const prevTokenRef = useRef(token);

  useEffect(() => {


    if (prevTokenRef.current !== null && token === null) {
    
      window.location.href = '/';
      prevTokenRef.current = token;

    }


    const fetchData = () => {
      const token = window.localStorage.getItem("token");
      if (token) {
        fetch("http://localhost:3005/api/pathcourse", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
          body: JSON.stringify({}),
        })
          .then((res) => res.json())
          .then((data) => {
            setUserPath(data.data);
          });
      }
    };
    
  
    // Gọi ngay lần đầu
    fetchData();
  
    // Sau đó lặp lại mỗi 10 giây
    const interval = setInterval(fetchData, 5000);
  
    // Cleanup: hủy interval khi component unmount
    return () => clearInterval(interval);
  }, []); // Dependency rỗng để tránh chạy lại không cần thiết




  const [message, setMessage] = useState('');

  useEffect(() => {
    // Địa chỉ API của backend Node.js
    // Docker Compose sẽ cho phép chúng ta truy cập qua IP của VPS và cổng 8080
    const apiUrl = `http://${window.location.hostname}:8080/api/message`;

    fetch(apiUrl)
      .then(response => response.json())
      .then(data => {
        setMessage(data.message);
      })
      .catch(error => {
        console.error('Lỗi khi gọi API:', error);
        setMessage('Không thể kết nối tới API backend.');
      });
  }, []);


  return (
    <Router>
    <Routes>
      {/* Main interface route */}
      {/* <Route path="/" element={<BodyKT />} /> */}

      {/* User related routes */}
      <Route path="/" element={<LoginRegis />} />
      <Route path= "/home" element = {<Home/>}/>
      <Route path= "/user" element = {<User/>}/>
      <Route path= "/toan" element = {<Toan/>}/>
      <Route path="/nguvan" element = {<Nguvan/>}/>
      <Route path="/tienganh" element = {<Tienanh/>}/>
      <Route  path="/hoahoc" element = {<Hoa/>}/>
      <Route path="/sinhhoc" element = {<Sinh/>}/>
      <Route path="/vatly" element = {<Vatly/>}/>
      <Route path="/lichsu" element={<Su/>} />
      <Route path="/dialy" element={<Dia/>} />
      <Route path="/admin" element = {<LoginAdmin/>}/>
      <Route path="/boardadmin" element = {<BoardAdmin/>}/>
      <Route path="/vaokhoahoc" element ={<Vaokhoahoc/>} /> 
      <Route path="/tailieu" element ={<Themtailieu/>} />
      <Route path={"/adminKhoahoc"} element ={<AdminCourse/>} />
      <Route path={"/naptien"} element ={<Naptien/>} />
      <Route path={"/category"} element ={<ListCate/>} />
      <Route path={"/hocthu"} element = {<Hocthu/>} />
      <Route path={"/khoahocdamua"} element = {<Hocreal/>} />
      {
        userPath.map((item, index) => (
          <Route
            key={index}
            path={`/course/${item.id}`}
            element={<RootCourse title={item.title} id ={item.id} introduceCourse ={item.introduceCourse} ContentCourse ={item.ContentCourse} price ={item.price} imgCourse ={item.imgCourse} author ={item.author} introduceAuthor ={item.introduceAuthor} priceSale ={item.priceSale} idCategory ={item.idCate }/>}
          />
        ))
      }


    </Routes>
  </Router>
  );
}

export default App;
