import React, { Component } from 'react'
import './hocthu.css'

export default class Hocthu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userData: "",
            Khoahoc: [],
            hienthi: false,
            soluong: 0,
            hienthi2: false,

        }

    }
    componentDidMount() {
        const token = window.localStorage.getItem("token");
        if (!token) {
            alert("Vui Lòng Đăng Nhập Lại ! ")
            window.location.href = "/";
        } else {
            fetch("http://localhost:3005/api/renderhocthu", {
                method: "POST",


                headers: {
                    "Content-Type": "application/json",
                    Accept: "application/json",

                },
                body: JSON.stringify({
                    token: window.localStorage.getItem("token"),
                }),
            })
                .then((res) => res.json())
                .then((data) => {


                    if (data.status === "ok") {
                        this.setState({ Khoahoc: data.data, soluong: data.soluong });
                        {

                        }
                    }
                    if (data.status === "error") {
                        alert(`${data.error}`);
                        window.location.href = "/category";
                    }
                });

        }
    }


    hienthi = () => {
        alert(" CHÚ Ý! - Do Một Số Chính Sách Bảo Mật,  Web Đã Sắp Xếp Thứ Tự Vị Trí Của Các Bài Học Theo Một Nguyên Tắc Bên Web,  Nên Mọi Người Học Đến Bài Nào Thì Tìm Số Bài Tiếp Theo Nhé")
        this.setState({ hienthi: true });
        this.setState({ hienthi2: true });
        if(this.state.soluong == 0) { 
  alert("Hiện Tại Không Có Khóa Học Nào Để Học Thử !")
        }

    }

    tathienthi = () => {
        this.setState({ hienthi: false });
        this.setState({ hienthi2: false });
    }
    hocthungay = () => { 
        alert("Khóa Học Này Đã Quá Số Lượng Học Thử, Vui Lòng Chọn Khóa Học Khác !")
    }













    render() {

        return (

            <div className="hocthu">
               <div className="home1">
          <nav className=" menuhome ">
            <div className=" text-center imganhhome1" >
              <img style={{ width: "200px", height: " 80px" }} src="https://i.imgur.com/VfUYz3F.png" alt="none" />
            </div>

            <div ><a className="itemmenu" href="/home">Trang Chủ <i className="fa-solid fa-house-user" /></a></div>
            <div ><a className="itemmenu" href="/naptien">Nạp Tiền <i className="fa-solid fa-landmark" /> </a></div>
            <div><a className="itemmenu" href="/hocthu">Học Thử <i className="fa-solid fa-file" /></a></div>
            <div  ><a className="itemmenu" href="/khoahocdamua" >Khóa Hoc Đã Mua <i className="fa-solid fa-book" />
            </a>
            </div>
            <div onClick={() => { window.location.href = "/user" }} className="fass user itemmenu text-center "><svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" className="bi bi-person-circle " viewBox="0 0 16 16">
              <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0" />
              <path fillRule="evenodd" d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8m8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1" />
            </svg>
            </div>


            <div className="dropdown">
              <a className="btn" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                <i className="fa-solid fa-bars" />

              </a>
              <ul className="dropdown-menu">
                <li><a className="dropdown-item" href="/home">Trang Chủ <i className="fa-solid fa-house-user" /></a></li>
                <li><a className="dropdown-item" href="/naptien">Nạp Tiền <i className="fa-solid fa-landmark" /></a></li>
                <li><a className="dropdown-item" href="/hocthu">Học Thử <i className="fa-solid fa-file" /></a></li>
                <li><a className="dropdown-item" href="/khoahocdamua">Khóa Hoc Đã Mua <i className="fa-solid fa-book" /></a></li>
                <li><a className="dropdown-item" href="/user"> Tài Khoản <i className="fa-solid fa-user" /></a></li>
              </ul>
            </div>



          </nav>
        </div>

                <div className="hocthu2 text-center">
                    <div className="bodyhocthu2">
                        <p>Quản Lý Danh Sách Học Thử </p>
                        <i>Số Lượng Khóa Học Đang Học Thử Là : {this.state.soluong}</i>
                        {this.state.hienthi2 ? <div className="bthocthu2">
                            <button className="btn btn-secondary" onClick={this.tathienthi}>Ẩn Ngay</button>
                        </div> :
                            <div className="bthocthu2">
                                <button className="btn btn-warning" onClick={this.hienthi}>Xem Khóa Học</button>
                            </div>}
                    </div>
                </div>

                <div className="hocthu3">


                    {this.state.hienthi ? (
                        <div className="roothocthu  accordion accordion-flush" id="accordionFlushExample">
                            {this.state.Khoahoc.map((itemKhoahoc) => (
                                <div  key={itemKhoahoc.id} className="accordion-item khoahocthu">
                                    <h2 key={itemKhoahoc.id} className="accordion-header">
                                        <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target={`#flush-${itemKhoahoc.id}`} aria-expanded="false" aria-controls={`flush-${itemKhoahoc.id}`}>
                                            {itemKhoahoc.title} <img  src={itemKhoahoc.imgCourse} alt="none" />
                                        </button>
                                    </h2>

                                    <div className="accordion-body">
                                        <div id={`flush-${itemKhoahoc.id}`} className="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
                                            <div key={itemKhoahoc.id} className="accordion-body">
                                                {itemKhoahoc.DanhMucCourse?.map((itemDanhMuc) => (
                                                    <div className="danhmuchocthu" key={itemDanhMuc.idDanhMuc}>
                                                        <h3 className=" text-warning fw-bold text-center" >{itemDanhMuc.title}</h3>
                                                        {

                                                            itemDanhMuc.Videos?.map((itemVideo) => (

                                                                <div className="videohocthu">
                                                                <i className="fw-bold"  key={itemVideo.idVideo}>{itemVideo.title}</i>
                                                                <button onClick={this.hocthungay} className="btn btn-success w-60">Xem </button>
                                                                <hr></hr>
                                                                </div>
                                                                
                                                            ))
                                                        }
                                                    </div>


                                                ))}


                                            </div>
                                        </div>
                                    </div>


                                </div>





                            ))}


                        </div>
                    ) : (
                        <h1>
                            <img className="loi404" src="https://i.imgur.com/tZAvDhB.png" alt="" />

                        </h1>
                    )}

                </div>


            </div>

        )
    }
}



