import React, { Component } from 'react'
import './Themtailieu.css'
export default class Themtailieu extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: "",
            linkimg: "",
            linkdowload: "",
            userDataTailieu: [],
        }
    }
    componentDidMount() {
        fetch("http://localhost:3005/api/laytailieu", {
            method: "POST",

            headers: {
                "Content-Type": "application/json",
                Accept: "application/json",


            },
            body: JSON.stringify({
                token: window.localStorage.getItem("token"),
                
            }),
        })
            .then((res) => res.json())
            .then((data) => {

                this.setState({ userDataTailieu: data.data });
                console.log(this.state.userDataTailieu);
            });

    }

    render() {
        return (
            <div className="container-tailieu">
                {/* MAY TINH */}
                <nav style={{ width: "100%", position:"fixed", backgroundColor: "white" }} className="menu menu-3">
                    <ul>
                       <li>  <img style={{width: "200px", height: "70px"}} src="https://i.imgur.com/VfUYz3F.png" alt="none" /></li>
                        <li><a href="/home" >Trang Chủ </a></li>
                        <li><a href="/tailieu">Danh Sách Tài Liệu Ôn Thi  Đại Học <i className="fa-solid fa-graduation-cap" /></a></li>
                        <li ><a href="/vaokhoahoc" >Truy Cập Khóa Học <i className="fa-solid fa-book" />
                        </a></li>
                        <li onClick={this.clickuser} className="user"><svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" className="bi bi-person-circle" viewBox="0 0 16 16">
                            <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0" />
                            <path fillRule="evenodd" d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8m8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1" />
                        </svg>

                        </li>
                    </ul>
                </nav>


                {/* DIEN THOAI */}
                <div style={{height: "100px", width:  "100vh", position: "fixed"}} className="dropdown">
  <a className="btn" style={{fontSize:"30px"}} href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
 <i className="fa-solid fa-bars" />

  </a>
  <ul className="dropdown-menu " style={{width: "300px", height: "300px", padding: "10px"}}>
    <li><a style={{fontSize: "20px"}} className="dropdown-item" href="/home" >Trang Chủ <i className="fa-solid fa-graduation-cap" /></a></li>
    <li><a style={{fontSize: "20px"}} className="dropdown-item"  href="/tailieu">Tài Liệu Ôn Thi  Đại Học <i className="fa-solid fa-graduation-cap" /></a></li>
    <li><a style={{fontSize: "20px"}} className="dropdown-item" href="/vaokhoahoc">Truy Cập Khóa Học <i className="fa-solid fa-book" /></a></li>
    <li><a style={{fontSize: "20px"}} className="dropdown-item" href="/user"> Tài Khoản <i className="fa-solid fa-user" /></a></li>
  </ul>
</div>

                
                <ul className="list-tailieu">

                    {this.state.userDataTailieu
                        .slice() // Tạo bản sao của mảng
                        .reverse() // Đảo ngược thứ tự
                        .map((item, index) => (
                            <div>
                            <li key={index}>
                                <img src={item.linkimg} alt={item.linkimg} />
                                <div>
                                    <h5 className="tieude">{item.title}</h5>
                                    
                                </div>
                                <a className="btn btn-success" href={item.linkdownload}>Tải Ngay</a>
                            </li>
                            </div> 
                        ))}
                </ul>
            </div>
        )
    }

}