import React, { Component } from 'react'
import './Rootcourse.css'
import { Swiper, SwiperSlide } from 'swiper/react';  //  npm i swiper
import { Autoplay } from 'swiper/modules';
import 'swiper/css';
export default class RootCourse extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userData: "",
      cateData: [],
      vocher: "",
      idCate: this.props.idCategory ,
      title: this.props.title,
      id: this.props.id,
      introduceCourse: this.props.introduceCourse,
      ContentCourse: this.props.ContentCourse,
      price: this.props.price,
      imgCourse: this.props.imgCourse,
      author: this.props.author,
      introduceAuthor: this.props.introduceAuthor,
      priceSale: this.props.priceSale,
      list1or2: false,
      diplayphobien: false,
       loading: false,
      // Thêm các trạng thái khác nếu cần
    };
  }
  list1or2 = () => {
    this.setState({ list1or2: true });
  }
  list2or1 = () => {
    this.setState({ list1or2: false });
  }



  componentDidMount() {
 
 
     
    

    fetch("http://localhost:3005/api/home", {
      method: "POST",

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",

      },
      body: JSON.stringify({
        token: window.localStorage.getItem("token"),
      }),
    })
      .then((res) => res.json())
      .then((data) => {

       
        if(data.status ===  "ok"){ 
          this.setState({ userData: data.data });
        } 
        if(data.status ===  "error"){ 
          alert("Vui lòng đăng nhập để tiếp tục");
          window.location.href = "/";
        }

      });
    




  }


   


   phobien(){ 
    const {idCategory} = this.props;

    fetch("http://localhost:3005/api/coursephobien", {
      method: "POST",

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",

      },
      body: JSON.stringify({
        token: window.localStorage.getItem("token"),
        id : idCategory,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        this.setState({ cateData: data.data });
       
      });
   }
  
  

  paysource = (e) => {
    e.preventDefault();
    this.setState({ loading: true });
    const vocher = this.state.vocher;
  
    fetch("http://localhost:3005/api/paycourse", {
      method: "POST",

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",

      },
      body: JSON.stringify({
        token: window.localStorage.getItem("token"),
        id: this.state.id,
        email: this.state.userData.email,
        vocher: vocher,
        folderName: this.state.id,


      }),
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.status == "ok") {
          this.setState({ loading: false });
          alert(`${data.success}`);
          window.location.reload();
        }
        if (data.status == "error") {
          this.setState({ loading: false });
          alert(`${data.error}`);
          window.location.reload();
        }

      });
  }



  hocthu = (e) => {
    e.preventDefault();

    console.log(this.state.cateData)
    fetch("http://localhost:3005/api/hocthu", {
      method: "POST",

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",

      },
      body: JSON.stringify({
        token: window.localStorage.getItem("token"),
        id: this.state.id,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.status == "ok") {
          
          alert(`${data.success}`);
          window.location.reload();
        }
        if (data.status == "error") {
          alert(`${data.error}`);
          window.location.reload();
        }

      });
  }

   display = () => {
    this.setState({ diplayphobien: true });
     this.phobien();
  }

  
  getPath = (e, id) => { 
    e.preventDefault();
    window.location.href = `/course/${id}`;

  }


  
   
  render() {


    return (
      <div>
        {/* Điện Thoại  */}
        <div className="parentakhoahoc   ctnroootkh">
          <div className="div1khoahoc  paydiv1">

            <div   className="luachon ">

              <form style={{backgroundColor: " rgb(178, 190, 201, 0)"}}  className="formpaymobile" onSubmit={this.paysource}>
                <div className="input-paymobile">
                  <input style={{ borderRadius: "40px" }} onChange={(e) => { this.setState({ vocher: e.target.value }); }} placeholder='Nhập Mã Vocher' />

                  <div>
                    <button className="btn btn-success" type="submit" >Mua Khóa Học</button>
                    <button className="btn btn-warning" onClick={this.hocthu}>Học Thử</button>

                  </div>

                </div>



              </form>
            </div>


            {/* Máy Tính */}
            <div className="luachonPC paymobieaa">

              <h4>Tổng Quan Khóa Học</h4>

              <p><i class="fa-solid fa-wallet"></i> Học Phí : {this.state.price} VND </p>
              <button className="btn btn-warning" onClick={this.hocthu}>Học Thử</button>
              <form style={{backgroundColor: " rgb(178, 190, 201, 0)"}}  onSubmit={this.paysource}>
                <input style={{ borderRadius: "40px" }} onChange={(e) => { this.setState({ vocher: e.target.value }); }} placeholder='Nhập Mã Vocher' />
                <button className="btn btn-success" type="submit" >Mua Khóa Học</button>
                <p><i className="fa-solid fa-clock" />Lưu ý: Nhập Vocher Vào Xong Rồi Bấn Mua Nhé !</p>

              </form>


            </div>




            <a href="/home" className="gobackhome"><i className="fa-solid fa-backward" />
              Trở Lại Trang Chủ</a>
            <h1>{this.state.title}</h1>
            <p className=" fw-bold text-success ">
              <i className="fa-regular fa-user"></i>{this.state.author} &nbsp; &nbsp;

              <i className="fa-regular fa-star stardv1" /> 4.9(100% Reviews)
              <br></br>
              <i className="fa-regular fa-clock" />  Năm 2025


            </p>
            <p>{this.state.introduceCourse}</p>
                  
            {this.state.loading ? 
     <div className="loading">
   <span className="loader"></span>
     </div>
     :null}
            <div className="giatien">
              <h4>GIẢM 25% CHỈ CÒN {this.state.price} VND</h4>


            </div>


          
          </div>

          <div className=" askvau div2khoahoc  ">
          <img src={this.state.imgCourse} alt=" none" />
            <div className="accordion accordion-flush " id="accordionFlushExample">

              <div className="accordion-item RootDiv2 ">
                <h2 className="accordion-header">
                  <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseOne" aria-expanded="false" aria-controls="flush-collapseOne">
                    1, Giới Thiệu Về Khóa Học
                  </button>
                </h2>
                <div id="flush-collapseOne" className="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
                  <div className="accordion-body">{this.state.introduceCourse} </div>
                </div>
              </div>


              <div className="accordion-item RootDiv2">
                <h2 className="accordion-header">
                  <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseTwo" aria-expanded="false" aria-controls="flush-collapseTwo">
                    2,  Nội Dung Khóa Học
                  </button>
                </h2>
                <div id="flush-collapseTwo" className="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
                  <div className="accordion-body"> {this.state.ContentCourse}</div>
                </div>
              </div>


              <div className="accordion-item RootDiv2">
                <h2 className="accordion-header">
                  <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseThree" aria-expanded="false" aria-controls="flush-collapseThree">
                    3, Thông Tin Giảng Viên
                  </button>
                </h2>
                <div id="flush-collapseThree" className="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
                  <div className="accordion-body">{this.state.introduceAuthor}</div>
                </div>
              </div>


              <div className="accordion-item RootDiv2">
                <h2 className="accordion-header">
                  <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapseFourd" aria-expanded="false" aria-controls="flush-collapseThree">
                    4, Thông Tin Về Nơi Cung Cấp Khóa Học "KHOAHOC.TOKYO"
                  </button>
                </h2>
                <div id="flush-collapseFourd" className="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
                  <div className="accordion-body">"KHOAHOC.TOKYO" là một nền tảng giáo dục trực tuyến nổi bật với sự đa dạng về lĩnh vực đào tạo, từ công nghệ thông tin, thiết kế đồ họa, đến phát triển bản thân và kinh doanh. Điểm mạnh của nơi đây nằm ở việc cập nhật xu hướng mới nhất, kết hợp giữa lý thuyết chuyên sâu và thực hành ứng dụng, giúp người học dễ dàng tiếp cận kiến thức thực tiễn. Các khóa học được thiết kế linh hoạt, phù hợp với mọi đối tượng từ sinh viên, người đi làm đến doanh nhân, đảm bảo tính cá nhân hóa cao.

Bên cạnh đó, "KHOAHOC.TOKYO" còn chú trọng xây dựng cộng đồng học tập sôi động, nơi học viên có thể trao đổi, kết nối với chuyên gia và cùng nhau phát triển. Với đội ngũ giảng viên giàu kinh nghiệm cùng hệ thống tài nguyên phong phú (video bài giảng, tài liệu, dự án mẫu), nền tảng này không chỉ là nơi "học" mà còn là không gian "trải nghiệm" và "sáng tạo". Đặc biệt, chính sách hỗ trợ học phí linh hoạt và cam kết đầu ra rõ ràng càng khẳng định uy tín của "KHOAHOC.TOKYO" trong lĩnh vực giáo dục trực tuyến tại Việt Nam.</div>
                </div>
              </div>






              <div className="accordion-item RootDiv2">
                <h2 className="accordion-header">
                  <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapse5" aria-expanded="false" aria-controls="flush-collapseThree">
                    5,  ĐIỀU KHOẢN SỬ DỤNG DỊCH VỤ
                  </button>
                </h2>
                <div id="flush-collapse5" className="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
                  <div className="accordion-body"> Khi tham gia sử dụng dịch vụ tại KHOAHOC.TOKYO, người dùng đồng ý tuân thủ các điều khoản sau đây:

Phạm vi áp dụng:
Điều khoản này quy định trách nhiệm, quyền lợi và nghĩa vụ giữa KHOAHOC.TOKYO và người dùng khi truy cập, đăng ký, hoặc sử dụng các khóa học trên nền tảng.

Đăng ký tài khoản:

Người dùng phải cung cấp thông tin chính xác, đầy đủ khi đăng ký và chịu trách nhiệm bảo mật tài khoản.

Nghiêm cấm việc chia sẻ tài khoản, gian lận hoặc sử dụng thông tin trái phép.

Quyền sở hữu trí tuệ:

Mọi nội dung khóa học (video, tài liệu, bài giảng) thuộc bản quyền của KHOAHOC.TOKYO hoặc đối tác cung cấp.

Người dùng không được sao chép, phân phối hoặc sử dụng vì mục đích thương mại khi chưa có sự cho phép bằng văn bản.

Thanh toán và hoàn tiền:

Học viên thanh toán học phí theo phương thức được niêm yết trên website.

Chính sách hoàn tiền áp dụng trong vòng 07 ngày kể từ ngày đăng ký nếu khóa học không đúng cam kết.

Trách nhiệm của nền tảng:

KHOAHOC.TOKYO cam kết duy trì chất lượng khóa học và hỗ trợ kỹ thuật 24/7.

Không chịu trách nhiệm với sự cố phát sinh từ lỗi mạng, thiết bị cá nhân hoặc hành vi vi phạm của người dùng.

Bảo mật thông tin:

Thông tin cá nhân của người dùng được mã hóa và chỉ sử dụng cho mục đích cải thiện dịch vụ, liên hệ hỗ trợ.

KHOAHOC.TOKYO không chia sẻ dữ liệu cho bên thứ ba trừ trường hợp được pháp luật yêu cầu.

Giải quyết tranh chấp:

Mọi tranh chấp phát sinh sẽ được ưu tiên giải quyết thông qua thương lượng.

Trường hợp không thống nhất, vụ việc sẽ được đưa ra tòa án có thẩm quyền tại Việt Nam.

Hiệu lực và sửa đổi:

Điều khoản có hiệu lực ngay khi được đăng tải trên website.

KHOAHOC.TOKYO có quyền điều chỉnh nội dung và sẽ thông báo trước ít nhất 15 ngày cho người dùng.

Lưu ý: Việc sử dụng dịch vụ đồng nghĩa với việc bạn đã đọc, hiểu và chấp thuận toàn bộ điều khoản trên. Mọi thắc mắc vui lòng liên hệ qua email support@khoahoc.tokyo</div>
                </div>
              </div>


              <div className="accordion-item RootDiv2">
                <h2 className="accordion-header">
                  <button className="accordion-button collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#flush-collapse6" aria-expanded="false" aria-controls="flush-collapseThree">
                    6,  Chính Sách Bảo Mật Và Quyền Riêng Tư
                  </button>
                </h2>
                <div id="flush-collapse6" className="accordion-collapse collapse" data-bs-parent="#accordionFlushExample">
                  <div className="accordion-body"> KHOAHOC.TOKYO cam kết bảo vệ thông tin cá nhân và quyền riêng tư của người dùng. Chính sách này mô tả cách chúng tôi thu thập, sử dụng và bảo mật dữ liệu khi bạn sử dụng dịch vụ trên nền tảng.

1. Thông Tin Thu Thập
Thông tin cá nhân: Họ tên, email, số điện thoại, địa chỉ, thông tin thanh toán khi bạn đăng ký tài khoản hoặc mua khóa học.

Dữ liệu truy cập: Địa chỉ IP, trình duyệt, thiết bị, lịch sử truy cập và cookies để phân tích hành vi người dùng.

Thông tin từ bên thứ ba: Nếu bạn đăng nhập qua mạng xã hội (Facebook, Google), chúng tôi thu thập thông tin công khai theo quyền truy cập được cấp.

2. Mục Đích Sử Dụng
Cung cấp, duy trì và cải thiện dịch vụ.

Xử lý giao dịch, gửi thông báo về khóa học, ưu đãi.

Phân tích dữ liệu để cá nhân hóa trải nghiệm học tập.

Liên hệ hỗ trợ và giải quyết tranh chấp (nếu có).

3. Bảo Mật Dữ Liệu
Mã hóa: Thông tin nhạy cảm (mật khẩu, thẻ thanh toán) được mã hóa bằng công nghệ SSL/TLS.

Hạn chế truy cập: Chỉ nhân viên được ủy quyền mới tiếp cận dữ liệu người dùng.

Lưu trữ: Dữ liệu được lưu trữ trên hệ thống máy chủ bảo mật cao và xóa sau khi không cần thiết.

4. Chia Sẻ Thông Tin
Đối tác dịch vụ: Chúng tôi chia sẻ thông tin với bên xử lý thanh toán, đơn vị vận chuyển (nếu có) để hoàn tất giao dịch.

Nghiêm túc tuân thủ pháp luật: Chỉ tiết lộ thông tin khi có yêu cầu từ cơ quan pháp lý có thẩm quyền.

Không bán dữ liệu: KHOAHOC.TOKYO không bán hoặc cho thuê thông tin người dùng vì mục đích thương mại.

5. Quyền Của Người Dùng
Truy cập và chỉnh sửa: Bạn có quyền xem, cập nhật hoặc xóa thông tin cá nhân trong mục Cài đặt tài khoản.

Từ chối tiếp thị: Hủy đăng ký nhận email quảng cáo bất kỳ lúc nào qua liên kết "Unsubscribe".

Khiếu nại: Liên hệ qua email privacy@khoahoc.tokyo nếu phát hiện vi phạm quyền riêng tư.

6. Cookies và Công Nghệ Theo Dõi
Cookies: Giúp ghi nhớ tùy chọn người dùng và phân tích lưu lượng truy cập.

Tắt cookies: Bạn có thể vô hiệu hóa cookies trong trình duyệt, nhưng một số tính năng trên website có thể bị giới hạn.

7. Cập Nhật Chính Sách
KHOAHOC.TOKYO có thể sửa đổi chính sách này để phù hợp với quy định mới hoặc mục tiêu kinh doanh.

Thay đổi sẽ được thông báo qua email hoặc banner trên website trước 15 ngày.

Liên Hệ:
Mọi thắc mắc về quyền riêng tư, vui lòng gửi yêu cầu đến privacy@khoahoc.tokyoaaaaaaaaaaaaaaaaaaaaaaaaaaaaa</div>
                </div>
              </div>
            </div>




          </div>
          <div className="Daudi3  text-center "> 
            <button  onClick={this.display}>Xem Thêm Các Khóa Học Liên Quan</button>
          </div>
          <div class=" di3root">
           
          <div className ="Thandi3    ">
          <ul className=" Courses   ">
          {this.state.diplayphobien ? <div className =" sesroot"> 

              {(this.state.cateData.listCate || []).map((item, index) => (
              
              
              
              
              <li >
            <div className ="img-course">
              <img  src={item.imgCourse} alt="none"/>
            </div>
            <div className ="title-course"> 
              <p>{item.title}</p>
            </div>
            <div className ="author-course"> 
              <p className ="text-center"><strong className ="fw-bold text-primary "><i className="fa-solid fa-chalkboard-user" /> Giảng Viên</strong>: {item.author}  <br></br> Lượt Mua: {item.luotmua}</p>
              <p className="text-center"><img src="https://i.imgur.com/ibdNqO0.png" /></p>
            </div>
            <div className="sale-course">
              <p className= "text-center">Giá Gốc: {item.priceSale} VND </p>
            </div>
            <div className="price-course">
             <p className ="text-center">Sale Chỉ Còn {item.price} VND </p>  
            </div>
            <div  > 
            <form style={{backgroundColor: " rgb(178, 190, 201, 0)"}} onSubmit={(e) => this.getPath(e, item.id)}  className="btn-course  ">
              <button   type="submit" >Xem Chi Tiết</button> 
            </form>
            
            </div>
            
          </li>
            ))}

          </div>: null}
        

        </ul> 
          </div>

           







          </div>




        </div>



      </div>
    )
  }
}


