import React, { Component } from 'react';
import './LoginRegis.css';
import { DotLottieReact } from '@lottiefiles/dotlottie-react';

export default class LoginRegis extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isSignUp: false,
      username: '',
      password: '',
      email: '',
      emailLOGIN: '',
      passwordLOGIN: '',
      hienthidangnhap: true,
      hienthidangki: false,
    };
  }

  handleSignUpClick = () => {
    this.setState({ 
      isSignUp: true,
      hienthidangnhap: false,
      hienthidangki: true 
    });
  };

  handleSignInClick = () => {
    this.setState({ 
      isSignUp: false,
      hienthidangnhap: true,
      hienthidangki: false 
    });
  };

  HandleSubmitDangKi = (e) => {
    const { username, password, email } = this.state;
if (!username || !password || !email) {
      alert("Vui lòng nhập đầy đủ thông tin.");
      return;
    }
    e.preventDefault();
    fetch("http://localhost:3005/api/dangki", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      body: JSON.stringify({
        username,
        password,
        email,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.status === "ok") {
          alert("Đăng Kí Thành Công.");
        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");
        }
      });
  }

  HandleSubmitDangNhap = (e) => {
    const { emailLOGIN, passwordLOGIN } = this.state;
    if (!emailLOGIN || !passwordLOGIN) {
      alert("Vui lòng nhập đầy đủ thông tin.");
      return;
    }
    e.preventDefault();
    fetch("http://localhost:3005/api/dangnhap", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
      body: JSON.stringify({
        passwordLOGIN,
        emailLOGIN,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        if (data.status === "ok") {
          alert("Đăng nhập thành công!");
          window.localStorage.setItem("token", data.data);
          window.location.href = "/home";
        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");
        }
      });
  }

  render() {
    return (
     <div className="LoginRegis">
       <div className="home">
        <DotLottieReact
          className="dotlottie-logina"
          src="https://lottie.host/1f2719c7-d9b8-4591-9966-4e501dc1e4d6/DOA7p4TV6K.lottie"
          loop
          autoplay
        />

        <div className={`container ${this.state.isSignUp ? "right-panel-active" : ""}`}>
          <div id="lottie-container" style={{ width: "600px", height: "600px" }}>
            {this.state.hienthidangnhap &&
              <DotLottieReact
                className="dotlottie-login"
                src="https://lottie.host/cb1adbf3-8e65-47a2-8422-2f551f78c4c1/ZJ1fC2JRRm.lottie"
                loop
                autoplay
              />
            }
            {this.state.hienthidangki &&
              <DotLottieReact
                className="dotlottie-register"
                src="https://lottie.host/b5021f81-6089-484e-aaf2-b4c812112304/pC7hHHhbcX.lottie"
                loop
                autoplay
              />
            }
          </div>

          <div className="form-container sign-up-container">
            <form onSubmit={this.HandleSubmitDangKi}>
              <h1>Đăng Kí</h1>
              <span>hoặc sử dụng email của bạn để đăng ký</span>
              <input type="text" placeholder="Tên Đăng Nhập Không Dấu" onChange={(e) => this.setState({ username: e.target.value })} />
              <input type="email" placeholder="Email" onChange={(e) => this.setState({ email: e.target.value })} />
              <input type="password" placeholder="Password" onChange={(e) => this.setState({ password: e.target.value })} />
              <button type="submit">Đăng Kí</button>
            </form>
          </div>

          <div className="form-container sign-in-container">
            <form onSubmit={this.HandleSubmitDangNhap}>
              <h1>Đăng Nhập</h1>
              <span>hoặc sử dụng tài khoản của bạn</span>
              <input type="email" placeholder="Email" onChange={(e) => this.setState({ emailLOGIN: e.target.value })} />
              <input type="password" placeholder="Password" onChange={(e) => this.setState({ passwordLOGIN: e.target.value })} />
              <a href="/forgot-password">Quên mật khẩu?</a>
              <button type="submit">Đăng Nhập</button>
            </form>
          </div>

          <div className="overlay-container">
            <div className="overlay">
              <div className="overlay-panel overlay-left">
                <h1>Chào mừng trở lại!</h1>
                <p>Muốn Truy Cập Vào Trang Chủ Bạn Vui Lòng Đăng Nhập </p>
                <button className="ghost" onClick={this.handleSignInClick}>
                  Đăng Nhập
                </button>
              </div>
              <div className="overlay-panel overlay-right">
                <h1>Xin Chào Quý Khách!</h1>
                <p>Nếu Bạn Chưa Có Tài Khoản Thì Hãy Bấm Vào Ô Bên Dưới</p>
                <button className="ghost" onClick={this.handleSignUpClick}>
                  Đăng Ký
                </button>
              </div>
            </div>
          </div>
          
         
        </div>
      </div>
      </div>
    );
  }
}