import React, { Component } from 'react'
import './board.css'
export default class BoardAdmin extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userData: '',
      kiemtrathongtin: false,
      xoathanhvien: true,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: false,
      themtailieu: false,
      xoatailieu: false,



      emailDelete: '',
      emailUpdate: '',
      sotienUpdate: '',
      emailNewPassHV: '',
      newPassHV: '',
      ttToan: false,
      ttVan: false,
      ttTienganh: false,
      ttSinh: false,
      ttLy: false,
      ttHoa: false,
      ttSu: false,
      ttDia: false,
      emailCapquyen: '',
      passAdminNew : '',
      mabimat: '',
      emailkiemtrathongtin: '',
      dataHocVien: '',
      titletailieu: '',
      linktailieu: '',
      linkanhtailieu: '',
      checktitlexoa: '',
      
    }
  }
  Quanlykhoahoca = (e) => { 
    e.preventDefault()
    console.log("Đã Click");
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      alert("Đăng Nhập Đi Rồi Anh Cho Vào Nha Bé ! ")
     } else { 
      window.location.href = "/adminKhoahoc";
     }
  }

  kichhoatToan = () => { 
    this.setState({ 
      ttToan: true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Toán";
    ul.appendChild(li);
  }
  kichhoatVan = () => { 
    this.setState({ 
      ttVan: true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Văn";
    ul.appendChild(li);
  }

  kichhoatTienganh = () => { 
    this.setState({ 
      ttTienganh : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Tiếng Anh";
    ul.appendChild(li);
  }


  kichhoatLy = () => { 
    this.setState({ 
      ttLy : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Lý";
    ul.appendChild(li);
  }

  kichhoatHoa = () => { 
    this.setState({ 
      ttHoa : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Hóa";
    ul.appendChild(li);
  }

  kichhoatSinh = () => {
    this.setState({
      ttSinh : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Sinh";
    ul.appendChild(li);
  }
  kichhoatSu = () => {
    this.setState({
      ttSu : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Sử";
    ul.appendChild(li);
  }

  kichhoatDia = () => {
    this.setState({
      ttDia : true,
    })
    const ul  = document.getElementById("Listkhoahoc");
    const  li = document.createElement("li");
    li.innerHTML = "Địa";
    ul.appendChild(li);
  }

  displayXoaThanhVien = () => {
    this.setState({
      themtailieu: false,
      kiemtrathongtin: false,
      xoathanhvien: true,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: false,
      xoatailieu: false,
    })
  }

  displayCapNhatSoDu = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: true,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: false,
      xoatailieu: false,
      themtailieu: false,
    })
  }
  Kiemtradata = (e) => {
    this.setState({
     kiemtrathongtin: true,
     xoathanhvien: false,
     capnhatsodu: false,
     passHocvien: false,
     capquyenkhoahoc: false,
     passAdmin: false,
     xoatailieu: false,
     themtailieu: false,
   })
 }



  displaypassHocvien = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: false,
      passHocvien: true,
      capquyenkhoahoc: false,
      passAdmin: false,
      xoatailieu: false,
      themtailieu: false,
    })
  }

  displaytruycap = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: true,
      passAdmin: false,
      xoatailieu: false,
      themtailieu: false,
    })
  } 

   displaypassAdmin = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: true,
      themtailieu: false,
      xoatailieu: false,
    })
  }
  displaythemtailieu = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: false,
      themtailieu: true,
      xoatailieu: false,
    })
    
  }
  displayxoatailieu = () => {
    this.setState({
      kiemtrathongtin: false,
      xoathanhvien: false,
      capnhatsodu: false,
      passHocvien: false,
      capquyenkhoahoc: false,
      passAdmin: false,
      themtailieu: false,
      xoatailieu: true,
    })

  }

  





  xoathanhvien = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("Đăng Nhập Đi Rồi Anh Cho Vào Nha Bé ! ")
     } else { 
   
    fetch("http://localhost:3005/api/xoathanhvien", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
        emailUser: this.state.emailDelete,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
       

        if (data.status === "ok") {


          alert("Xóa Thành Công Học Viên !");  // Đăng nhập thành công
          window.location.reload();
        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");

        }
      });
    }
  }


  componentDidMount() {
    const token = window.localStorage.getItem("tokenADMIN");
     if(!token){ 
      window.location.href = "/admin";
      alert("Đăng Nhập Đi Rồi Anh Cho Vào Nha Bé ! ")
     }  
  
    fetch("http://localhost:3005/api/BoardAdmin", {
      method: "POST",
      
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",

      },
      body: JSON.stringify({
        tokenADMIN: window.localStorage.getItem("tokenADMIN"),
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        
        this.setState({ userData: data.data });

      });
     
  }

  DangXuat = () => { 
    window.localStorage.removeItem("tokenADMIN");
    window.location.reload();
    window.location.href = "/";
    
  }






  capnhatsodu = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("Đăng Nhập Đi Rồi Anh Cho Vào Nha Bé ! ")
     } else {
    fetch("http://localhost:3005/api/capnhatsodu", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
        emailUser: this.state.emailUpdate,
        SoduCapnhat: this.state.sotienUpdate,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
       

        if (data.status === "ok") {


          alert("Đã Cập Nhật Số Dư Của Học Viên Thành Công !");  // Đăng nhập thành công
          window.location.reload();
        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");

        }
      });
    }
  }



  
  PassnewHocVien = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("Đăng Nhập Đi Rồi Anh Cho Vào Nha Bé ! ")
     } else {
    fetch("http://localhost:3005/api/capnhatpasssinhvien", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
        emailUser: this.state.emailNewPassHV,
        passwordCapnhat: this.state.newPassHV,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        

        if (data.status === "ok") {


          alert("Đã Cập Nhật Mật Khẩu Của Học Viên Thành Công !");  // Đăng nhập thành công
          window.location.reload();
        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");

        }
      });
    }
  }



  KichHoatKhoaHoc = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("Đăng Nhập Đi Rồi Anh Cho Vào Nha Bé ! ")
     } else { 

     
    fetch("http://localhost:3005/api/kichhoatkhoahoc", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
        emailUser: this.state.emailCapquyen,
        toan: this.state.ttToan,
        van: this.state.ttVan,
        tienganh: this.state.ttTienganh,
        sinh: this.state.ttSinh,
        ly: this.state.ttLy,
        hoa: this.state.ttHoa,
        su: this.state.ttSu,
        dia: this.state.ttDia,

      }),
    })
      .then((res) => res.json())
      .then((data) => {
        

        if (data.status === "ok") {


          alert("Đã Cập Nhật Các Khóa Học Thành Công Của Học Viên Thành Công !");  // Đăng nhập thành công
          window.location.reload();
        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");

        }
      });
    }
  }


  DoipassAdmin = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("Đăng Nhập Đi Rồi Anh Cho Vào Nha Bé ! ")
     } else {
    fetch("http://localhost:3005/api/changepasswordAdmin", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
       tokenADMIN: window.localStorage.getItem("tokenADMIN"),
       passwordCapnhat: this.state.passAdminNew,
       Mabimat: this.state.mabimat,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
     

        if (data.status === "ok") {


          alert("Đã Cập Nhật Các Khóa Học Thành Công Của Học Viên Thành Công !");  // Đăng nhập thành công
          window.location.reload();
        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");

        }
      });
    }
  }

  Kiemtrathongtinhocvien = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("Đăng Nhập Đi Rồi Anh Cho Vào Nha Bé ! ")
     } else {
    fetch("http://localhost:3005/api/kiemtrathongtinhocsinh", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
       tokenADMIN: window.localStorage.getItem("tokenADMIN"),
       emailUser: this.state.emailkiemtrathongtin,
      
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        

        if (data.status === "ok") {
          this.setState({
            dataHocVien: data.data,
          })
       
         
        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");

        }
      });
    }
  }


  Themtailieu = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("Đăng Nhập Đi Rồi Anh Cho Vào Nha Bé ! ")
     } else {
    fetch("http://localhost:3005/api/themtailieu", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
       tokenADMIN: window.localStorage.getItem("tokenADMIN"),
       title: this.state.titletailieu,
      linkimg: this.state.linkanhtailieu,
      linkdownloadfn: this.state.linktailieu, 
      
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        

        if (data.status === "ok") {
          alert("Đã Thêm Tài Liệu Thành Công !");  // Đăng nhập thành công
          window.location.reload();
       
         
        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");

        }
      });
    }
  }



  Xoatailieu = (e) => {
    e.preventDefault()
    const token = window.localStorage.getItem("tokenADMIN");
    if(!token){
      window.location.href = "/admin";
      alert("Đăng Nhập Đi Rồi Anh Cho Vào Nha Bé ! ")
     } else {
    fetch("http://localhost:3005/api/xoatailieu", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
       tokenADMIN: window.localStorage.getItem("tokenADMIN"),
       checktitlexoa: this.state.checktitlexoa,
     
      
      }),
    })
      .then((res) => res.json())
      .then((data) => {
        

        if (data.status === "ok") {
          alert("Đã Xóa Tài Liệu Thành Công !");  // Đăng nhập thành công
          window.location.reload();
       
         
        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");

        }
      });
    }
  }

 

  



  render() {
    return (
      <div>

        <div className="parentad">
          <div className="div1ad">
            <h1>TRANG QUẢN LÝ</h1>
            <ul>
              <li> <i className="fa-solid fa-landmark" /> Doanh Thu: {this.state.userData.doanhthu}</li>
              <li><i className="fa-solid fa-users" />Số Lượng Thành Viên: {this.state.userData.danso} </li>

            </ul>
            <p>phần này bạn chỉ cần bấn vào các lựa chọn bên dưới là nó hiện ra bảng xử lý nha !<br></br>Nếu chỉnh sai hay sao thì cứ biểu tôi nhé, tôi vào code tôi sửa lại cho </p>
            <button onClick={this.DangXuat}>Đăng Xuất</button>
          </div>
          <div className="div2ad">
            <div className="listButton">
             
              <button onClick={this.displayXoaThanhVien}>Xóa Thành Viên</button>
              <button onClick={this.displayCapNhatSoDu}>Cập Nhật Số Dư</button>
              <button onClick ={this.displaypassHocvien}>Mật Khẩu Học Sinh</button>
              <button onClick ={this.displaytruycap}>Cấp Quyền Khóa Học</button>
              <button onClick= {this.displaypassAdmin}>Thay Mật Khẩu ADMIN</button>
              <button onClick= {this.Kiemtradata}>Kiểm Tra Thông Tin Học Sinh</button>
              <button onClick={this.displaythemtailieu}>Thêm Tài Liệu</button>
              <button onClick={this.displayxoatailieu} >Xóa Tài Liệu</button>
              <button onClick={this.Quanlykhoahoca}>Quản Lý Khóa Học</button>
             
            </div>
          </div>
          <div className="div3ad">


            {/* Xóa Thành Viên */}
            {this.state.xoathanhvien ?
              <div className="xoathanhvien">
                <h1>Xoá Thành Viên</h1>
                <form onSubmit={this.xoathanhvien}>
                  <input type="text" className="form-control" placeholder="Nhập Email Của Học Viên" onChange={(e) => { this.setState({ emailDelete: e.target.value }); }} />
                  <button type="submit">Xóa Ngay</button>
                </form>
              </div> : null}

            {/* Cập Nhật Số Dư */}
            {this.state.capnhatsodu ?
              <div>

                <form onSubmit={this.capnhatsodu}>
                  <input type="text" className="form-control" placeholder="Nhập Email Của Học Viên" onChange={(e) => { this.setState({ emailNewPassHV: e.target.value }); }} />
                  <input type="text" className="form-control" placeholder="Nhập Số Tiền Cần Cập Nhật " onChange={(e) => { this.setState({ sotienUpdate: e.target.value }) }} />
                  <button type="submit">Cập Nhật Ngay</button>
                </form>

              </div>
              : null}

              {/* Thay Mật Khẩu Học Sinh */}

              {this.state.passHocvien ? 
              <div> 
                <h1>Thay Đổi Mật Khẩu Học Sinh</h1>
                <form onSubmit ={this.PassnewHocVien}>
                <input type="text" className="form-control" placeholder="Nhập Email Của Học Viên" onChange={(e) => { this.setState({ emailNewPassHV: e.target.value }); }} />
                <input type="text" className="form-control" placeholder="Mật Khẩu Mới Cho Học Sinh" onChange={(e) => { this.setState({ newPassHV: e.target.value }); }} />
                 <button type="submit">Cập Nhật Ngay</button>
                </form>
              </div>
              :null}



            {/* Cấp Quyền Truy Cập Khóa Học */}
            {this.state.capquyenkhoahoc ? 
            <div  className ="themkhoahoc"> 
              <h1>Cấp Quyền Truy Cập Khóa Học</h1>

              <form onSubmit={this.KichHoatKhoaHoc} > 
                <input type="text" className="form-control" placeholder="Nhập Email Của Học Viên" onChange={(e) => { this.setState({ emailCapquyen: e.target.value }); }} />
              <button type = "submit">Kích Hoạt Ngay</button>
              </form>
                <ul>
                  <li><a  onClick={this.kichhoatToan}>Thêm Toán</a></li>
                  <li><a onClick ={this.kichhoatVan}>Thêm Ngữ Văn</a></li>
                  <li><a onClick = {this.kichhoatTienganh}>Thêm Tiếng Anh</a></li>
                  <li><a onClick ={this.kichhoatLy}>Thêm Vật Lý</a></li>
                  <li><a onClick={this.kichhoatHoa}>Thêm Hóa</a></li>
                  <li><a onClick={this.kichhoatSinh}>Thêm Sinh Học</a></li>
                  <li><a onClick ={this.kichhoatSu}>Thêm Lịch Sử</a></li>
                  <li><a onClick = {this.kichhoatDia}>Thêm Địa Lý</a></li>
                </ul>
                
                <ul id="Listkhoahoc">
                <h1 className ="text-center text-light">Danh Sách Các Khóa Học Được Thêm Vào</h1>
                </ul>

              
            </div>:null}

             {/* ĐỔI MẬT KHẨU ADMIN */}
             {this.state.passAdmin ? 
            <div>
              <h1>Đổi Mật Khẩu ADMIN QUẢN LÝ</h1>
              <form onSubmit ={this.DoipassAdmin}>
               
                <input type = "text" className = "form-control" placeholder= "Nhập Mật Khẩu Mới" onChange ={(e) => {this.setState({passAdminNew:e.target.value})}} />
                <input type = "text" className ="form-control" placeholder= "Nhập Khóa Bí Mật"  onChange ={(e) => {this.setState({mabimat:e.target.value})}} />
                <button type="submit" className= "btn btn-success">Đổi Mật Khẩu</button>
              </form>
            </div>
             :null}

             {/* KIỂM TRA THÔNG TIN HỌC SINH */}
           {this.state.kiemtrathongtin ? <div>
            <form onSubmit ={this.Kiemtrathongtinhocvien}>
               
               <input type = "text" className = "form-control" placeholder= "Nhập Email Học Sinh Cần Kiểm Tra " onChange ={(e) => {this.setState({emailkiemtrathongtin:e.target.value})}} />
              
               <button type="submit" className= "btn btn-success">Tra Cứu Ngay</button>
             </form>
             <ul style ={{width:"100%",height:"100%",display:"flex",flexDirection:"column",justifyContent:"center",alignItems:"center"}}>
              <h3>Thông Tin Của Học Sinh</h3>
              <li>Email Đăng Kí: {this.state.dataHocVien.email } </li>
              <li>Số Dư: {this.state.dataHocVien.sodu }</li>
              <li>Tên Tài Khoản: {this.state.dataHocVien.username}</li>
              <li>Môn Toán: {this.state.dataHocVien.checkToan ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
              <li>Môn Văn: {this.state.dataHocVien.checkVan ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>} </li>
              <li>Môn Tiếng Anh: {this.state.dataHocVien.checkTienganh ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
              <li>Môn Sinh: {this.state.dataHocVien.checkSinh ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
               <li> Môn Lý: {this.state.dataHocVien.checkLy ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
               <li> Môn Hóa: {this.state.dataHocVien.checkHoa ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
               <li> Môn Địa: {this.state.dataHocVien.checkDia ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
               <li> Môn Sử: {this.state.dataHocVien.checkSu ? <span> 
            <i className="fa-solid fa-circle-check" />
              </span> : <span> <i className="fa-solid fa-circle-xmark" /></span>}</li>
             </ul>
           </div>:null}

           {/* THÊM TÀI LIỆU */}

           {this.state.themtailieu ? <div>
            <h1>Thêm Tài Liệu</h1>
              <form onSubmit ={this.Themtailieu}>
               
                <input type = "text" className = "form-control" placeholder= "Nhập Tiêu Đề " onChange ={(e) => {this.setState({titletailieu:e.target.value})}} />
                <input type = "text" className ="form-control" placeholder= "Nhập Link Ảnh"  onChange ={(e) => {this.setState({linkanhtailieu:e.target.value})}} />
                <input type = "text" className ="form-control" placeholder= "Nhập Link Tải "  onChange ={(e) => {this.setState({linktailieu:e.target.value})}} />
                <button type="submit" className= "btn btn-success">Thêm Tài Liệu</button>
              </form>
           </div>: null}

           {/* XOA TAI LIEU */}
           {this.state.xoatailieu ? 
           <div>
              <h1>Xóa Tài Liệu</h1>
              <form onSubmit ={this.Xoatailieu}>
                <input type = "text" className = "form-control" placeholder= "Nhập Title Tài Liệu Cần Xóa" onChange ={(e) => {this.setState({checktitlexoa:e.target.value})}} />
                <button type="submit" className= "btn btn-success">Xóa Tài Liệu</button>
              </form>
           </div>
           :null}
            
      


          </div>
        </div>




      </div>
    )
  }
}
