import React, { Component } from 'react'
import  './listcategory.css'
export default class ListCate extends Component {
  constructor(props) {
    super(props);
    this.state = {
      CateData: [],
      userData: "",
      courseData: [],
      hienthicourse: true,
 
    };
  }
  componentDidMount() {
    const token = window.localStorage.getItem("token");
    if (!token) {
      window.location.href = "/";
      alert("Vui lòng đăng nhập để tiếp tục");
    }


    fetch("http://localhost:3005/api/rendercate", {
      method: "POST",

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",

      },
      body: JSON.stringify({
        token: window.localStorage.getItem("token"),
      }),
    })
      .then((res) => res.json())
      .then((data) => {

        this.setState({ CateData: data.data });

        console.log(data.data);
      });


      fetch("http://localhost:3005/api/home", {
        method: "POST",
  
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
  
        },
        body: JSON.stringify({
          token: window.localStorage.getItem("token"),
        }),
      })
        .then((res) => res.json())
        .then((data) => {
  
         
          if(data.status ===  "ok"){ 
            this.setState({ userData: data.data });
          } 
          if(data.status ===  "error"){ 
            alert("Vui lòng đăng nhập để tiếp tục");
            window.location.href = "/";
          }
  
        });




  }

  renderCate = (e, id) => {
    e.preventDefault();
    fetch("http://localhost:3005/api/rendercourse", {
      method: "POST",

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",

      },
      body: JSON.stringify({
        token: window.localStorage.getItem("token"),
        id: id,
      }),
    })
      .then((res) => res.json())
      .then((data) => {

        this.setState({ courseData: data.data });
        this.setState({ hienthicourse: false });

      });
  }


  getPath = (e, id) => { 
    e.preventDefault();
    window.location.href = `/course/${id}`;

  }





  render() {
    return (
      <div className="containerCate">
        <div className="containerHeader">
        <div className="home1">
          <nav className=" menuhome ">
            <div className=" text-center imganhhome1" >
              <img style={{ width: "200px", height: " 80px" }} src="https://i.imgur.com/VfUYz3F.png" alt="none" />
            </div>

            <div ><a className="itemmenu" href="/home">Home <i className="fa-solid fa-house-user" /></a></div>
            <div ><a className="itemmenu" href="/naptien">Nạp Tiền <i className="fa-solid fa-landmark" /> </a></div>
            <div><a className="itemmenu" href="/hocthu">Học Thử <i className="fa-solid fa-file" /></a></div>
            <div  ><a className="itemmenu" href="/khoahocdamua" >Khóa Hoc Đã Mua <i className="fa-solid fa-book" />
            </a>
            </div>
            <div onClick={() => { window.location.href = "/user" }} className="fass user itemmenu text-center "><svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" className="bi bi-person-circle " viewBox="0 0 16 16">
              <path d="M11 6a3 3 0 1 1-6 0 3 3 0 0 1 6 0" />
              <path fillRule="evenodd" d="M0 8a8 8 0 1 1 16 0A8 8 0 0 1 0 8m8-7a7 7 0 0 0-5.468 11.37C3.242 11.226 4.805 10 8 10s4.757 1.225 5.468 2.37A7 7 0 0 0 8 1" />
            </svg>
            </div>


            <div className="dropdown">
              <a className="btn" href="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                <i className="fa-solid fa-bars" />

              </a>
              <ul className="dropdown-menu">
                <li><a className="dropdown-item" href="/home">Trang Chủ <i className="fa-solid fa-house-user" /></a></li>
                <li><a className="dropdown-item" href="/naptien">Nạp Tiền <i className="fa-solid fa-landmark" /></a></li>
                <li><a className="dropdown-item" href="/hocthu">Học Thử <i className="fa-solid fa-file" /></a></li>
                <li><a className="dropdown-item" href="/khoahocdamua">Khóa Hoc Đã Mua <i className="fa-solid fa-book" /></a></li>
                <li><a className="dropdown-item" href="/user"> Tài Khoản <i className="fa-solid fa-user" /></a></li>
              </ul>
            </div>



          </nav>
        </div>

           

          <div className="contaithann">
         
         
            <div className="listCate">
           
              <ul className ="categorys">
                
                {this.state.CateData
                
                  .map((item, index) => (
             <form className ="category" onSubmit={(e) => this.renderCate(e, item.idCate)} > <button className ="btn btn-secondary"><i className ="titleCate">{item.idCate}, {item.titleCate}</i></button> </form> 
                  ))}
              </ul>
            </div>

            <div className="titlelistcourse"><i >Hãy Lựa Chọn Danh Mục </i> <hr></hr></div>

            <div className="listCourse">
              <ul className="coursess">
                {this.state.hienthicourse ?
                  <div className="kh3">
                    <img className="loi404" src="https://i.imgur.com/tZAvDhB.png" alt="" />

                  </div> : <div> 
                    <ul className ="Courses">

                    {this.state.courseData.listCate
                      .slice() // Tạo bản sao của mảng
                      .reverse() // Đảo ngược thứ tự
                      .map((item, index) => (
                        <li>
                            <div className ="icon-course">
                         <i className="fa-solid fa-arrow-down" />

                            </div>
                          <div className ="img-course">
                            <img  src={item.imgCourse} alt="none"/>
                          </div>
                          <div className ="title-course"> 
                            <p>{item.title}</p>
                          </div>
                          <div className ="author-course"> 
                            <p className ="text-center"><strong className ="fw-bold text-primary "><i className="fa-solid fa-chalkboard-user" /> Giảng Viên</strong>: {item.author}  <br></br> Lượt Mua: {item.luotmua}</p>
                            <p className="text-center"><img src="https://i.imgur.com/ibdNqO0.png" /></p>
                          </div>
                          <div className="sale-course">
                            <p className= "text-center">Giá Gốc: {item.priceSale} VND </p>
                          </div>
                          <div className="price-course">
                           <p className ="text-center">Sale Chỉ Còn {item.price} VND </p>  
                          </div>
                          <form onSubmit={(e) => this.getPath(e, item.id)}  className="btn-course">
                            <button type="submit" >Xem Chi Tiết</button>
                          </form>
                          

                         

                        </li>
                      ))}


                    </ul>
                  

                  </div>}
              </ul>
            </div>

          </div>

        </div>
      </div>
    )
  }
}
