import React, { Component } from 'react'
import './admin.css'
export default class LoginAdmin extends Component {
    constructor(props) {
        super(props);
        this.state = {
            emailADMIN: '',
            passwordADMIN: '',
        };
    }
    HandleSubmitDangNhap = (e) => {
        const { emailADMIN, passwordADMIN } = this.state;
        e.preventDefault();
        fetch(`http://localhost:3005/api/loginadmin`, {
          method: "POST",
          
    
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
           
          },
          body: JSON.stringify({
            emailADMIN: this.state.emailADMIN,
            passwordADMIN: this.state.passwordADMIN,
    
          }),
        })
          .then((res) => res.json())
          .then((data) => {
            
            if (data.status === "ok") {
              alert("Đăng nhập thành công!");  // Đăng nhập thành công
              window.localStorage.setItem("tokenADMIN", data.data);
              window.location.href = "/BoardAdmin";
            
    
            } else {
              alert("Có lỗi xảy ra! Vui lòng thử lại.");
            }
          });
      }
    render() {
        return (
            <div className="login-container">

                <form onSubmit={this.HandleSubmitDangNhap} id="loginForm">
                <h2>Đăng Nhập Quản Lý</h2>
                    <div className="input-group adminaa">
                     
                        <input type="email" placeholder="Email" onChange={(e) => { this.setState({ emailADMIN: e.target.value }); }} />
                    </div>
                    <div className="input-group adminaa">
                       
                        <input type="password" placeholder="Password" onChange={(e) => { this.setState({ passwordADMIN: e.target.value }); }} />
                    </div>
                    <button type="submit " className="btn btn-success">Đăng Nhập</button>
                    <p className="message" id="message" />
                </form>
            </div>

        )
    }
}
