  constructor(props) {
    super(props);
    this.state = {
      userData: '',
      list1or2: true,
    };
  }



  componentDidMount() {
 
    fetch("http://localhost:3005/api/home", {
      method: "POST",
      
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",

      },
      body: JSON.stringify({
        token: window.localStorage.getItem("token"),
      }),
    })
      .then((res) => res.json())
      .then((data) => {

    
        this.setState({ userData: data.data });

      });
  }

  HandleSubmitThemToan = () => {


    fetch("http://localhost:3005/api/themTA", {
      method: "POST",
      

      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
       
      },
      body: JSON.stringify({
        Soluongtienganh: this.state.userData.english,
        emailUser: this.state.userData.email,
      }),
    })
      .then((res) => res.json())
      .then((data) => {
      

        if (data.status === "ok") {
         
          window.location.reload()
          alert("Thêm Môn Tiếng Anh Vào Giỏ Hàng Thành Công!");  // Đăng nhập thành công

        } else {
          alert("Có lỗi xảy ra! Vui lòng thử lại.");

        }
      });
  }

  displaylist1 = (e) => {
    e.preventDefault();
    this.setState({ list1or2: true })
  }
  displaylist2 = (e) => {
    e.preventDefault();
    this.setState({ list1or2: false })
  }
