import React, { Component } from 'react'

import './Vaokhoahoc.css'
import Toan from './Mon/Toan';
import Tienganh from './Mon/Tienganh';
import Nguvan from './Mon/Nguvan';
import Vatly from './Mon/Vatly';
import Hoahoc from './Mon/Hoahoc';
import Lichsu from './Mon/Lichsu';
import Dialy from './Mon/Dialy';
import Sinhhoc from './Mon/Sinhhoc';
export default class Vaokhoahoc extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userData: "",
            toan: false,
            nguvan: false,
            tienganh: false,
            ly: false,
            hoa: false,
            sinh: false,
            su: false,
            dia: false,
            macdinh: true,

        };
    }
    componentDidMount() {
        const token = window.localStorage.getItem("token");
        if (!token) {
            window.location.href = "/";
            alert("Vui lòng đăng nhập để tiếp tục");
        }

       
        fetch("http://localhost:3005/api/home", {
            method: "POST",
            
            headers: {
                "Content-Type": "application/json",
                Accept: "application/json",

            },
            body: JSON.stringify({
                token: window.localStorage.getItem("token"),
            }),
        })
            .then((res) => res.json())
            .then((data) => {
               
                this.setState({ userData: data.data });

            });
    }


    displaytoan = () => {
        this.setState({ toan: this.state.userData.checkToan });
        this.setState({ tienganh: false });
        this.setState({ nguvan: false});
        this.setState({ ly: false });
        this.setState({ hoa: false });
        this.setState({ sinh: false });
        this.setState({ dia: false });
        this.setState({su: false });
        this.setState({ macdinh: false });
    }
    displaytienganh = () => {
        this.setState({ tienganh: this.state.userData.checkTienganh });
        this.setState({ nguvan: false});
        this.setState({ ly: false });
        this.setState({ hoa: false });
        this.setState({ sinh: false });
        this.setState({ toan: false });
        this.setState({ dia: false });
        this.setState({su: false });
        this.setState({ macdinh: false });
    }
    displaynguvan = () => {
        this.setState({ nguvan: this.state.userData.checkVan });
        this.setState({ ly: false });
        this.setState({ hoa: false });
        this.setState({ sinh: false });
        this.setState({ toan: false });
        this.setState({ tienganh: false });
        this.setState({ dia: false });
        this.setState({su: false });
        this.setState({ macdinh: false });
    }
    displayvatly = () => {
        this.setState({ ly: this.state.userData.checkLy });
        this.setState({ hoa: false });
        this.setState({ sinh: false });
        this.setState({ toan: false });
        this.setState({ tienganh: false });
        this.setState({ nguvan: false });
        this.setState({ dia: false });
        this.setState({su: false });
        this.setState({ macdinh: false });
    }
    displayhoahoc = () => {
        this.setState({ hoa: this.state.userData.checkHoa });
        this.setState({ sinh: false });
        this.setState({ toan: false });
        this.setState({ tienganh: false });
        this.setState({ nguvan: false });
        this.setState({ ly: false });
        this.setState({ dia: false });
        this.setState({su: false });
        this.setState({ macdinh: false });
    }
    displaylichsu = () => {
        this.setState({ su: this.state.userData.checkSu });
        this.setState({ sinh: false });
        this.setState({ toan: false });
        this.setState({ tienganh: false });
        this.setState({ nguvan: false });
        this.setState({ ly: false });
        this.setState({ dia: false });
        this.setState({hoa: false });
        this.setState({ macdinh: false });
    }
    displaydialy = () => {
        this.setState({ dia: this.state.userData.checkDia });
        this.setState({ sinh: false });
        this.setState({ toan: false });
        this.setState({ tienganh: false });
        this.setState({ nguvan: false });
        this.setState({ ly: false });
        this.setState({ hoa: false });
        this.setState({su: false });
        this.setState({ macdinh: false });
    }
    displaysinh = () => {
        this.setState({ sinh: this.state.userData.checkSinh });
        this.setState({ toan: false });
        this.setState({ tienganh: false });
        this.setState({ nguvan: false });
        this.setState({ ly: false });
        this.setState({ hoa: false });
        this.setState({ dia: false });
        this.setState({su: false });
        this.setState({ macdinh: false });
    }
    render() {
        return (

            <div className="containerkh">
                <div className="kh1">
                    <h1>Quản Lý Kho Học</h1>
                </div>
                <div className="kh2">
                    <ul>
                        <li onClick={this.displaytoan}>Môn Toán</li>
                        <li onClick={this.displaytienganh}>Môn Tiếng Anh</li>
                        <li onClick={this.displaynguvan}>Môn Ngữ Văn</li>
                        <li onClick={this.displayvatly}>Môn Vật Lý</li>
                        <li onClick={this.displayhoahoc}>Môn Hóa Học</li>
                        <li onClick={this.displaysinh}>Môn Sinh Học</li>
                        <li onClick ={this.displaydialy}>Môn Địa Lý</li>
                        <li onClick={this.displaylichsu}>Môn Lịch Sử</li>
                    </ul>
                </div>
                <div className="kh3">

                    {this.state.toan ? <div>  <Toan /> </div> : null}
                    {this.state.tienganh ? <div><Tienganh /></div> : null}
                    {this.state.nguvan ? <div> <Nguvan/></div> : null}
                    {this.state.sinh ? <div><Sinhhoc/></div> : null}
                    {this.state.ly ? <div><Vatly/></div>: null }
                    {this.state.hoa ? <div> <Hoahoc/></div> : null}
                    {this.state.su ? <div><Lichsu/></div> : null}
                    {this.state.dia ? <div><Dialy/></div> : null}
                    {this.state.macdinh ? <div>
                        <h3 className="text-center fw-bold">HIỆN TẠI CHƯA CÓ MÔN NÀO HIỂN THỊ, HÃY LỰA CHỌN MÔN NGAY !</h3>
                    <img className="loi404" src="https://i.imgur.com/tZAvDhB.png" alt="" />
                   </div> : null}
                </div>
            </div>




        )
    }
}
